<template>
  <div class="generate-doc">
    <div class="custom-tree-container">
      <div class="block">
        <el-button @click="addDoc" class="addDoc">生成接口文档</el-button>
        <el-tree
          :data="data"
          show-checkbox
          node-key="id"
          default-expand-all
          :expand-on-click-node="false">
          <span class="custom-tree-node" slot-scope="{ node, data }">
            <span>{{ node.label }}</span>
            <el-button
              size="mini"
              plain
              @click="() => append(data)"
              class="add">
              +
            </el-button>
          </span>
        </el-tree>
      </div>
    </div>
    <div class="right">
      <el-button>保存</el-button>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column
          prop="name"
          label="参数名称"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="address"
          label="name"
          align="center"
        >
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
let id = 1000
export default {
  name: 'GenerateDoc',
  data() {
    const data = [
      {
        id: 1,
        label: '接口类型',
        children: [{
          id: 4,
          label: '接口名称',
          children: [{
            id: 7,
            label: '参数'
          }, {
            id: 8,
            label: '参数'
          }]
        }]
      },
      {
        id: 2,
        label: '接口类型',
        children: [{
          id: 5,
          label: '接口名称',
          children: [{
            id: 9,
            label: '参数'
          }, {
            id: 10,
            label: '参数'
          }]
        }]
      },
      {
        id: 3,
        label: '接口类型',
        children: [{
          id: 6,
          label: '接口名称',
          children: [{
            id: 11,
            label: '参数'
          }, {
            id: 12,
            label: '参数'
          }]
        }]
      }
    ]
    return {
      data: JSON.parse(JSON.stringify(data)),
      tableData: [{
        name: '参数类型',
        address: <select style="width: 200px">
          <option>vchar</option>
          <option>int</option>
          <option>dable</option>
        </select>
      }, {
        name: '是否必填',
        address: <div>
          <input type="checkbox" value="isRequired" name="required" /> <span style="margin-right: 30px">是</span>
          <input type="checkbox" value="isRequired" name="required" /> <span>否</span>
        </div>
      }, {
        name: '备注',
        address: '用户姓名'
      }]
    }
  },
  methods: {
    // 添加自定义节点
    append(data) {
      console.log(data)
      const newChild = { id: id++, label: '自定义', children: [] }
      if (!data.children) {
        this.$set(data, 'children', [])
      }
      data.children.push(newChild)
    },
    addDoc () {
      this.$router.back()
    }
  }
}
</script>

<style lang="scss">
  .generate-doc {
    display: flex;
    padding-top: 50px;
    .add {
      display: none;
    }
  }
  .generate-doc .custom-tree-node:hover {
    .add {
      display: block;
    }
  }
  .generate-doc .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
  }
  .generate-doc .el-tree {
    width: 200px;
    height: 700px;
    border: 1px solid #ededed;
  }
  .generate-doc .el-tree-node__content {
    height: 36px;
  }
  .right {
    width: 700px;
    position: relative;
    left: 200px;
  }
  .addDoc {
    margin-bottom: 20px;
  }
</style>
